@import "../main.scss";

.TextContainer {
  width: 100%;
  font-family: "Arbel Regular";
  color: $textColor;
  font-size: 14pt;
  z-index: 5;
}

.content {
  overflow-y: scroll;
  height: 100vh;
  background-color: $backgroundColor;
  padding: 0 8% 0 15%;
  z-index: 1;
}

.chapter {
  padding-bottom: 30px;
  line-height: 26pt;
  direction: rtl;
  z-index: 1;
  white-space: pre-wrap;
}

span {
  padding-left: 8px;
}

span.active {
  background-color: transparent;
  font-family: "Arbel Bold";
}

span.currentActive {
  animation: fadeBackground 2s 1;
  animation-timing-function: ease-out;
  animation-delay: 0.1s;
}

span.searched {
  background-color: rgba($shineColor, 0.5);
}

@keyframes fadeBackground {
  0% {
    background-color: transparent;
  }
  20% {
    background-color: $shineColor;
  }
  100% {
    background-color: transparent;
  }
}

div.sticky {
  position: sticky;
  background-color: $backgroundColor;
  line-height: 22pt;
  padding: 22pt 0 20pt 0;
  color: lighten($textColor, 20%);
  top: -1px;
  font-family: "Rubik Regular";
  font-size: 12pt;
  border-bottom: 1px solid $midColor;
  z-index: 1;
}

div.details {
  display: flex;
  position: relative;
  justify-content: space-between;
  direction: ltr;
}

.infoText {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: $infoBackgroundDarker;
}

.stickyContainer {
  padding: 0 8% 0 15%;
  z-index: 10;
}

.infoSticky {
  position: sticky;
  height: 114px;
  top: -1px;
  border-bottom: 1px solid $lightColor;
  z-index: 1;
}

.infoBoxDetails {
  top: 40%;
  right: 30%;
}

.logo {
  position: absolute;
  width: 220px;
  height: 200px;
  top: 66px;
  right: 9%;
  background-image: url("./logo.svg");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 11;
}

.desc {
  font-family: "Arbel Regular";
  position: absolute;
  padding: 0 8% 0 15%;
  font-size: 14pt;
  color: $darkColor;
  line-height: 18pt;
  top: 125px;
}

strong {
  font-family: "Arbel Bold";
}

.resizeIcon {
  position: absolute;
  height: 20px;
  width: 20px;
  transform: scaleX(-1);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  mask-repeat: no-repeat;
  mask-size: contain;
  top: 10px;
  left: 60px;
  z-index: 3;
  transition: mask-image 0.25s ease-out, background-color 0.25s ease-out;
  background-color: lighten($textColor, 20%);

  &:hover {
    background-color: $shineColor;
  }
}

.expand {
  mask-image: url("./expand-alt-solid.svg");
}

.compress {
  mask-image: url("./compress-alt-solid.svg");
}

.episode {
  padding: 6% 0;
  border-bottom: 1px solid $lightColor;
}

.episodeBanner {
  width: 100%;
  background-color: $lightColor;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5%;
}

.episodeHeader {
  font-family: "Rubik Bold";
  font-size: 20pt;
  padding: 50px 0;
}

.episodeRow {
  display: flex;
  position: relative;
  justify-content: space-between;
}

.rowUnit {
  padding: 10px 0;
  display: flex;
  position: relative;
  justify-content: space-between;
}

.rowHeader {
  font-family: "Arbel Bold";
}

.rowDetails {
  width: 65%;
}

.inline {
  display: inline-block;
}

.offset {
  position: absolute;
  padding-right: 20%;
}
