@import "../main.scss";

::-webkit-scrollbar {
  width: 0;
}

.emotionsMap {
  display: inline-block;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.label {
  fill: $lightColor;
  font-family: "Rubik Regular";
  font-size: 24pt;
}

.regularDot {
  cursor: pointer;
  transition: fill 0.25s ease-out, opacity 0.25s ease-out;
}

.nonActiveDot {
  fill: $darkColor;
}

.lowOpacity {
  opacity: 0.6;
}

.emotionsContent {
  display: inline-block;
  position: absolute;
  top: 0;
}

.diamond {
  fill: none;
  stroke: $lightColor;
  stroke-miterlimit: 10;
}

.infoBoxEmotion {
  top: 5%;
  left: 20%;
}

.infoBoxSearch {
  top: 5%;
  right: 30px;
}

.connector {
  stroke: $shineColor;
  fill: transparent;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: dash 1s ease-out forwards;
}

@keyframes dash {
  0% {
    stroke-dashoffset: 1000;
  }
  100% {
    stroke-dashoffset: -1000;
  }
}
