@import "./components/main.scss";

.GraphFrame {
  position: fixed;
  width: 50%;
  height: 45%;
  left: 0;
  bottom: 0;
  cursor: grab;
  z-index: 1;
  &:active {
    cursor: grabbing;
  }
}

.EmotionsFrame {
  position: fixed;
  width: 50%;
  height: 55%;
  background-color: $midColor;
  left: 0;
  top: 0;
  text-align: left;
}

.textFrame {
  z-index: 4;
  position: fixed;
  background-color: $midColor;
  transition: right 0.25s ease-out;
  width: 50%;
  height: 100vh;
}

.frontBackground {
  z-index: 8;
  position: fixed;
  background-color: $lightColor;
  transition: right 0.25s ease-out;
  width: 50%;
  height: 100vh;
}

.scrollBarContainer {
  transition: left 0.25s ease-out;
  z-index: 20;
  height: 100vh;
  position: fixed;
  width: 50px;
}

::-webkit-scrollbar {
  width: 0;
}

body {
  overflow: hidden;
  direction: rtl;
  background-color: $lightColor;
}

.hoverBox {
  position: absolute;
  width: 180px;
  font-size: 11pt;
  font-family: "Rubik Regular";
  color: $textColor;
  transition: left 0.25s ease-out;
  padding: 10px;
  pointer-events: none;
}

.hoverBoxText {
  background-color: darken($backgroundColor, 3%);
  z-index: -3;
}

.hoverBoxScrollBar {
  background-color: $backgroundColor;
  z-index: -3;
}

.hoverHeader {
  font-family: "Arbel Regular";
  border: 1px solid $midColor;
  font-size: 12pt;
  border-right: 0;
  border-left: 0;
  line-height: 16pt;
  padding: 20px 3px;
}

.hoverPlace {
  color: lighten($textColor, 20%);
  padding: 10px 3px;
  direction: ltr;
}

.hoverEmotions {
  color: lighten($textColor, 20%);
  padding: 10px 3px;
  white-space: pre-wrap;
  line-height: 18pt;
}

.hoverDate {
  direction: ltr;
  padding-left: 3px;
  color: lighten($textColor, 20%);
}

.infoContainer {
  position: fixed;
  background-color: transparent;
  height: 40px;
  width: 40px;
  left: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 3px;
  transition: border 0.25s ease-out;
  z-index: 8;
}

.infoIcon {
  position: fixed;
  cursor: pointer;
  mask-repeat: no-repeat;
  mask-size: contain;
  mask-image: url("./question.svg");
  top: 35px;
  left: 30px;
  width: 40px;
  height: 40px;
  z-index: 8;
  transition: background-color 0.25s ease-out;
}

.infoImg {
  background-color: $lightColor;
  mask-image: url("./question.svg");
  &:hover {
    background-color: $shineColor;
  }
}

.exitImg {
  mask-image: url("./exit.svg");
  background-color: $shineColor;
  &:hover {
    background-color: $lightColor;
  }
}

.activeDot {
  fill: $shineColor;
  opacity: 1;
}

.fullOpacity {
  opacity: 1;
}

.searched {
  fill: $shineColor;
}

.info {
  position: absolute;
  background-color: $infoBackground;
  width: 100%;
  height: 100%;
  z-index: 9;
}

.fadeIn {
  animation: fade-in 0.25s 1;
  animation-timing-function: ease-out;
}

.fadeOut {
  animation: fade-out 0.25s 1;
  animation-timing-function: ease-out;
}

.infoBox {
  position: absolute;
  width: 180px;
  font-family: "Arbel Regular";
  font-size: 14pt;
  line-height: 18pt;
  direction: rtl;
  color: $darkColor;
  background-color: transparent;
  padding: 10px;
  text-align: right;
}

.infoHeader {
  font-family: "Rubik Bold";
  text-align: right;
  padding-bottom: 10px;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
