@import "../main.scss";

.autoCompleteContainer {
  position: absolute;
  text-align: right;
  background-color: transparent;
  font-size: 18pt;
  top: 35px;
  width: 130px;
  height: 40px;
  right: 7%;
  display: flex;
  flex-direction: row-reverse;
  border: 0.5px solid $lightColor;
  border-left: 0;
  border-right: 0;
  z-index: 5;
}

.react-autosuggest__container {
  font-family: "Arbel Regular";
  background-color: transparent;
  text-align: right;
  border: none;
  padding: 12px 0;
  font-size: 14pt;

  &:focus {
    outline: none;
  }
}

.react-autosuggest__input {
  font-family: "Arbel Regular";
  background-color: transparent;
  text-align: right;
  color: $backgroundColor;
  width: 90%;
  font-size: 14pt;
  vertical-align: middle;
  border: none;
  &:focus {
    outline: none;
  }
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 40px;
  width: 100%;
  background-color: $lightColor;
  font-family: "Arbel Regular";
  font-size: 14pt;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  color: $midDarkColor;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  max-height: 40vh;
  overflow-y: auto;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 10px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: $darkColor;
}

:placeholder-shown {
  color: $lightColor;
}

::-webkit-input-placeholder {
  color: $lightColor;
}

.searchIcon {
  position: absolute;
  right: -25px;
  top: 10px;
  width: 20px;
  height: 20px;
  mask-repeat: no-repeat;
  mask-size: contain;
  transition: background-color 0.25s ease-out;
  background-color: $lightColor;
  mask-image: url("./search.svg");
}

.clearIcon {
  position: absolute;
  top: 15px;
  width: 10px;
  height: 10px;
  cursor: pointer;
  mask-repeat: no-repeat;
  mask-size: contain;
  background-color: $lightColor;
  mask-image: url("./return.svg");
  &:active {
    background-color: $shineColor;
  }
}

.active {
  animation: fadeColor 0.25s 1;
  animation-timing-function: ease-out;
}

@keyframes fadeColor {
  0% {
    background-color: $lightColor;
  }
  10% {
    background-color: $shineColor;
  }
  100% {
    background-color: $lightColor;
  }
}
