@import "../main.scss";

.scrollBar {
  position: absolute;
  width: 50px;
  bottom: 0;
  left: 0;
  height: 100%;
  background-color: darken($lightColor, 5%);
  cursor: pointer;
  z-index: 5;
}

.thumb {
  position: absolute;
  height: 0;
  width: 80%;
  margin: 0 10%;
  background-color: $midDarkColor;
}

.marker {
  position: absolute;
  height: 3px;
  width: 80%;
  margin: 0 10%;
  background-color: rgba($shineColor, 0.7);
  transition: height 0.25s ease-out, background-color 0.25s ease-out;
  &:hover {
    height: 8px;
    background-color: $shineColor;
  }
}

.episodeMarker {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid $shineColor;
}

.markerFadeIn {
  animation: fadeIn 0.6s 1;
  animation-timing-function: ease-in-out;
}

.markerFadeOut {
  animation: fadeOut 0.6s 1;
  animation-timing-function: ease-in-out;
  opacity: 0;
}

.hoverMarker {
  position: absolute;
  height: 3px;
  width: 80%;
  margin: 0 10%;
  top: 0;
}

.brightMarker {
  background-color: $midColor;
}

.darkMarker {
  background-color: $lightColor;
}

#active {
  background-color: $shineColor;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.infoScrollBar {
  border-right: 1px solid $lightColor;
  border-left: 1px solid $lightColor;
}

.movingDateContainer {
  position: absolute;
  right: 0;
  width: 100%;
}

.movingDate {
  position: absolute;
  left: 105%;
  white-space: nowrap;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  text-orientation: mixed;
  font-family: "Arbel Regular";
  font-size: 14pt;
}

.brightDate {
  color: darken($lightColor, 5%);
}

.darkDate {
  color: $midColor;
}

.transparentMarker {
  background-color: transparent;
}

.shineDate {
  color: $shineColor;
}
