@import "../main.scss";

.playlistIconContainer {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 80px;
  top: 60px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  z-index: 2;
}

.iconContainer {
  position: absolute;
  color: $textColor;
  right: 8%;
  top: 35px;
  z-index: 2;
  border: 1.5px solid $midColor;
  border-left: 0;
  border-right: 0;
  &:hover {
    border: 1.5px solid $shineColor;
    border-left: 0;
    border-right: 0;
    color: $shineColor;
  }
}

.icon {
  color: $midColor;
  padding: 0 3px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}

.playlistContainer {
  position: absolute;
  width: 50%;
  background-color: $midDarkColor;
  font-family: "Arbel Regular";
  font-size: 14pt;
  line-height: 32pt;
  transition: right 0.25s ease-out;
  padding-bottom: 10%;
  z-index: 4;
}

.songsContainer {
  margin: 5% 8% 15% 17%;
  overflow-y: scroll;
  height: 100vh;
}

.songContainer {
  color: $lightColor;
  display: flex;
  position: relative;
  justify-content: space-between;
  direction: ltr;
  border-bottom: 0.5px solid $lightColor;
  padding: 0 5px;
  transition: background-color 0.25s ease-out, color 0.25s ease-out;
  cursor: pointer;
  &:hover {
    color: $shineColor;
  }
}

.songActive {
  background-color: $darkColor;
  color: $midDarkColor;
}

.waveformContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 100%;
  cursor: pointer;
  background: transparent;
}

.wave {
  position: absolute;
  width: 70%;
  margin-left: 4%;
  padding-bottom: 10%;
  height: 90px;
  top: 20px;
  z-index: 6;
}

.playButton {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 40px;
  width: 30px;
  height: 30px;
  right: 8%;
  background: $lightColor;
  border-radius: 50%;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 3px;
  &:hover {
    background: $backgroundColor;
  }
}

.returnIcon {
  position: absolute;
  color: $shineColor;
  top: 6px;
  left: 70px;
  cursor: pointer;
}
